<template>
  <div>
    <v-nav></v-nav>
    <div class="infor">
      <img src="./my.png" alt="" class="b-img">
      <div class="con">
        <img :src="infor.avatar" alt="" class="avatar">
        <div class="ct">
          <h3>{{infor.nickname}}</h3>
        </div>
      </div>
    </div>
    <div class="my-link">
      <router-link to="/edit">
        <div class="right">
          <img src="../../assets/next.png" alt="">
        </div>
        <img src="./die1.png" alt="" class="dot">
        <span>资料编辑</span>
      </router-link >
      <router-link to="/order">
        <div class="right">
          <img src="../../assets/next.png" alt="">
        </div>
        <img src="./die2.png" alt="" class="dot">
        <span>我的订单</span>
      </router-link>
      <a @click="showConfirm">
        <div class="right">
          <img src="../../assets/next.png" alt="">
        </div>
        <img src="./die3.png" alt="" class="dot">
        <span>客服中心</span>
      </a>
      <router-link to="/feedback">
        <div class="right">
          <img src="../../assets/next.png" alt="">
        </div>
        <img src="./die4.png" alt="" class="dot">
        <span>意见反馈</span>
      </router-link>
    </div>
    <div class="confirm" v-show="confirm">
      <div class="bg"></div>
      <div class="confirm-box">
        <p>拨打客服热线</br>{{infor.telephone}}</p>
        <div class="option">
          <span @click="hide">取消</span>
          <a :href="'tel:'+infor.telephone" @click="hide">拨打</a>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import nav from '@/components/nav/nav'
  export default{
    components:{
      'v-nav': nav
    },
    data(){
      return {
        confirm: false,
        infor: {}
      }
    },
    created(){
      let obj = this.doQs({
        time: TIMER,
        uid: loginInfor.uid,
        hashid: loginInfor.hashid,
        hash: 'e785f07736bde4d62b77d03214d29647'
      });
      this.$axios.post(APIURL + 'Api/Center/index',obj).then((res)=>{
        console.log(res)
        this.infor = res.data.Data;
      })
    },
    methods:{
      showConfirm(){
        this.confirm = true;
      },
      hide(){
        this.confirm = false;
      }
    }
  }
</script>
<style scoped>
  .infor{
    position: relative;
  }
  .b-img{
    width: 100%;
    height: 3rem;
  }
  .infor .con{
    width: 6rem;
    height: 1.2rem;
    position: absolute;
    left: 0.6rem;
    top: 50%;
    margin-top: -0.6rem;
    display: flex;
  }
  .infor .con img{
    flex: 0 0 1.2rem;
    width: 1.2rem;
    height: 1.2rem;
    border-radius: 50%;
    border: solid 1px #fff;
    box-sizing: border-box;
  }
  .infor .con .ct{
    flex: 1;
    padding-left: 0.2rem;
  }
  .infor .con .ct h3{
    color: #fff;
    font-size: 18px;
    margin-top: 0.1rem;
  }
  .my-link{
    margin-top: 0.8rem;
  }
  .my-link a{
    display: block;
    padding: 0.25rem 0.3rem;
    background: #fff;
    height: 0.4rem;
    line-height: 0.4rem;
    position: relative;
  }
  .my-link a .right{
    float: right;
  }
  .my-link a .right img{
    width: 0.44rem;
    height: 0.44rem;
  }
  .my-link a .dot{
    width: 0.4rem;
    vertical-align: middle;
  }
  .my-link a span{
    vertical-align: middle;
    margin-left: 0.1rem;
  }
  .my-link a:after{
    position: absolute;
    left: 0;
    bottom: -1px;
    width: 100%;
    height: 1px;
    border-top:solid 1px #ddd;
    transform: scaleY(0.5);
    content: '';
  }
  .confirm{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 88;
  }
  .confirm .bg{
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.7);
  }
  .confirm-box{
    background: #fff;
    position: absolute;
    left: 50%;
    top: 40%;
    width: 5rem;
    transform: translate(-50%,-50%);
    text-align: center;
    font-size: 15px;
    border-radius: 0.2rem;
  }
  .confirm-box p{
    margin: 0.5rem 0.3rem;
  }
  .option{
    position: relative;
    height: 0.9rem;
    display: flex;
  }
  .option:before{
    position: absolute;
    left: 0;
    top: -1px;
    width: 100%;
    height: 1px;
    border-top:solid 1px #ddd;
    transform: scaleY(0.5);
    content: '';
  }
  .option span{
    flex: 1;
    height: 0.9rem;
    line-height: 0.9rem;
    color: #999;
    position: relative;
  }
  .option span:after{
    position: absolute;
    right: -1px;
    top: 0;
    width: 1px;
    height: 100%;
    border-right:solid 1px #ddd;
    transform: scaleX(0.5);
    content: '';
  }
  .option a{
    flex: 1;
    height: 0.9rem;
    line-height: 0.9rem;
    color: #0076ff;
  }
</style>


