<template>
  <div>
    <v--header :title="'意见反馈'"></v--header>
    <div class="feedback">
      <textarea name="" v-model="text" placeholder="请输入您的意见"></textarea>
    </div>
    <div class="fd-btn" @click="submit">提交</div>
  </div>
</template>
<script>
  import header from '@/components/header/header'
  export default{
    components:{
      'v--header': header
    },
    data(){
      return {
        text:''
      }
    },
    methods:{
      submit(){
        if(!this.text){
          this.webTip('请输入您的意见');
          return false;
        }
        let obj = this.doQs({
          time: TIMER,
          uid: loginInfor.uid,
          hashid: loginInfor.hashid,
          content: this.text,
          hash: 'c8bb69fee40f114551362426f6adc0be'
        });
        this.$axios.post(APIURL + 'Api/Center/addFeedback',obj).then((res)=>{
          console.log(res)
          let $this = this;
          if(res.data.Code == 0){
            this.webTip('提交成功',function(){
              $this.$router.go(-1);
            })
          }
        })
      }
    }
  }
</script>
<style scoped>
.feedback{
  padding: 1.1rem 0.3rem 0 0.3rem;
}
.feedback textarea{
  width: 100%;
  height: 3rem;
  box-sizing: border-box;
  font-size: 14px;
  resize: none;
  border: none;
  border-radius: 5px;
  padding: 10px;
}
.fd-btn{
  position: fixed;
  right: 0;
  top: 0;
  width: 1.2rem;
  height: 0;
  padding: 0.45rem 0;
  text-align: center;
  font-size: 14px;
  font-weight: 500;
  z-index: 888;
  line-height: 0;
  color: #98c347;
}
</style>


