<template>
  <div>
    <div class="search-header">
      <div class="s-back" @click="back">
        <img src="../../assets/back1.png" alt="">
      </div>
      <div class="search-box">
        <form action="" ref="form" @submit="search">
          <input type="search" class="s-input" placeholder="输入关键词搜索您想要的商品" v-model="key">
        </form>
        <img src="./search.png" alt="" class="s-dot">
      </div>
    </div>
    <div class="search-contain">
      <ul class="g-list" v-show="!noData">
        <li v-for="item in list">
          <router-link :to="'/detail?id='+item.id">
          <div class="con">
            <img :src="item.goodsimg" alt="" class="g-img">
            <h3>{{item.goodsname}}</h3>
            <p>
              <span>￥{{item.goodsprice}}</span>
            </p>
          </div>
          </router-link>
        </li>
      </ul>
      <div class="no-data" v-show="noData">
        暂无{{key}}相关内容
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    data(){
      return {
        noData: false,
        key: '',
        list:[]
      }
    },
    created(){
      let myKey = this.$route.query.key;
      if(myKey){
        this.key = myKey;
        this.getGood();
      }
    },
    methods:{
      back(){
        this.$router.push('/index');
      },
      getGood(){
        //获取商品
        let obj1 = this.doQs({
          time: TIMER,
          cid: '',
          page: 1,
          hash: 'bc2dea0c01a584d9f26d8ea1afda8e60',
          keyword: this.key
        });
        this.$axios.post(APIURL + 'Api/Shop/goodsList',obj1).then((res)=>{
          console.log(res)
          if(res.data.Code == '0'){
            this.list = res.data.Data.list;
            if(this.list.length == 0){
              this.noData = true;
            }else{
              this.noData = false;
            }
          }else{
            this.webTip(res.data.Msg)
          }
        })
      },
      search(){
        this.getGood();
      }
    }
  }
</script>
<style scoped>
  .search-header{
    position: fixed;
    left: 0;
    top: 0;
    padding: 0.15rem 0.4rem 0.15rem 1rem;
    width: 100%;
    height: 0.9rem;
    box-sizing: border-box;
    background: #fff;
    z-index: 99;
  }
  .search-header .s-back{
    position: absolute;
    left: 0;
    top: 0;
    width: 0.44rem;
    height: 0.44rem;
    padding: 0.23rem;
  }
  .search-header .s-back img{
    width: 100%;
    height: 100%;
  }
  .search-box{
    position: relative;
    background: #f7f7f7;
    border-radius: 4px;
  }
  .search-box input{
    appearance: none;
    border: none;
    margin: 0;
    padding: 0;
    width: 100%;
    line-height: 0.4rem;
    border-radius: 4px;
    padding: 0.1rem 0.2rem 0.1rem 0.6rem;
    box-sizing: border-box;
    font-size: 14px;
    background: #f7f7f7;
  }
  .search-box .s-dot{
    position: absolute;
    left: 0.1rem;
    top: 0.1rem;
    width: 0.4rem;
  }
  .search-contain{
    padding-top: 0.9rem;
  }
  .g-list{
    margin-top: 0.1rem;
  }
  .g-list li{
    padding: 0.3rem;
    position: relative;
    height: 1.6rem;
    display: flex;
    background: #fff;
  }
  .g-list li:after{
    position: absolute;
    left: 0;
    bottom: -1px;
    width: 100%;
    height: 1px;
    border-top:solid 1px #eee;
    transform: scaleY(0.5);
    content: '';
  }
  .g-list li .left{
    flex: 0 0 0.6rem;
    height: 100%;
    position: relative;
  }
  .g-list li .con{
    flex: 1;
    height: 100%;
    position: relative;
    padding-left: 1.8rem;
  }
  .g-list li .left .r{
    width: 0.36rem;
    height: 0.36rem;
    border: solid 1px #ddd;
    border-radius: 50%;
    box-sizing: border-box;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
  }
  .g-list li .left .r img{
    position: absolute;
    left: -1px;
    top: -1px;
    width: 0.36rem;
    height: 0.36rem;
    display: none;
  }
  .g-list li .con .g-img{
    position: absolute;
    left: 0;
    top: 0;
    width: 1.6rem;
    height: 1.6rem;
  }
  .g-list li .con h3{
    font-size: 15px;
    line-height: 0.4rem;
    height: 0.8rem;
    margin-top: 0.1rem;
    overflow-y: hidden;
    text-align: justify;
  }
  .g-list li .con p{
    font-size: 14px;
    color: #999;
    margin-top: 0.2rem;
  }
  .g-list li .con p span{
    color: #d30000;
    float: right;
    font-weight: 500;
  }
  .no-data{
    margin: 0.5rem;
    text-align: center;
    line-height: 0.4rem;
    color: #999;
  }
</style>


