<template>
  <div>
    <div class="detai-header">
      <div class="d-back" @click="back">
        <img src="../../assets/back2.png" alt="">
      </div>
      <router-link to="trolley" class="h-right">
        <img src="../../assets/trolley.png" alt="">
      </router-link>
      商品详情
    </div>
    <div class="detail-contain">
      <swiper class="detail-banner" :options="swiperOption" ref="mySwiper">
        <swiper-slide v-for="(item,index) in infor.pics" :key="index">
          <img :src="item" alt="">
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
      <div class="d-price">
        ￥{{infor.goodsprice}}
        <em>原价<i>￥{{infor.originalprice}}</i></em>
      </div>
      <div class="d-title">
        {{infor.goodsname}}
      </div>
      <div class="number" @click="showPop">
        <div class="right">
          <div class="text">{{cNum}}</div>
          <img src="../../assets/next.png" alt="">
        </div>
        选择数量
      </div>
      <div class="detail-con">
        <div class="title">
          <img src="../../assets/die.png" alt="" class="die">
          <span>产品介绍</span>
        </div>
        <div class="intro" v-if="infor.goods_parameters">
          <div class="i-tiem" v-for="item in infor.goods_parameters">
            <div class="t1">{{item.pname}}</div>
            <div class="t2">{{item.pvalue}}</div>
          </div>
        </div>
        <h3>商品特色</h3>
        <div class="d-content" v-html="infor.content"></div>
        <!-- <div class="d-imgs">
          <img src="./01.png" alt="">
        </div> -->
      </div>
    </div>
    <div class="detail-bottom">
      <span class="add-t" @click="addTrolley">加入购物车</span>
      <span class="buy-now" @click="buy">立即购买</span>
    </div>
    <div class="detail-pop" v-show="popShow">
      <transition name="fade">
      <div class="bg" v-show="pcShow" @click="hidePop"></div>
      </transition>
      <transition name="move">
      <div class="pop-con" v-show="pcShow">
        <div class="pop-infor">
          <div class="img">
            <img src="./01.png" alt="">
          </div>
          <div class="price">
            ￥279<em>原价<i>￥299</i></em>  
          </div>
          <h3>名称</h3>
        </div>
        <div class="pop-num">
          <div class="right">
            <span class="randuce" @click="randuce">-</span>
            <span class="num">{{num}}</span>
            <span class="add" @click="add">+</span>
          </div>
          数量
        </div>
      </div>
      </transition>
    </div>
  </div>
</template>
<script>
  export default{
    data(){
      return {
        swiperOption:{
          notNextTick: true,
          pagination: {
            el: '.swiper-pagination',
            bulletClass : 'my-bullet',
            bulletActiveClass: 'my-bullet-active'
          },
          initialSlide: 0
        },
        popShow: false,
        pcShow: false,
        num: 1,
        gId: '',
        infor: {},
        cNum: '',
        reqing: false
      }
    },
    created(){
      this.gId = this.$route.query.id;
      let obj = this.doQs({
        time: TIMER,
        goods_id: this.gId,
        hash: '318187973c265936150856b6c6a67a7b'
      });
      this.$axios.post(APIURL + 'Api/Shop/goodsDetail',obj).then((res)=>{
        console.log(res)
        if(res.data.Code == '0'){
          this.infor = res.data.Data;
        }else{
          this.webTip(res.data.Msg)
        }
        console.log(this.infor)
      })
    },
    methods: {
      back(){
        this.$router.go(-1);
      },
      showPop() {
        this.popShow = true;
        this.pcShow = true;
        this.cNum = this.num;
      },
      hidePop() {
        let $this = this;
        this.pcShow = false;
        setTimeout(function(){
          $this.popShow = false;
        },300)
      },
      randuce() {
        if(this.num == 1){
          return false;
        }
        this.num--;
        this.cNum = this.num;
      },
      add() {
        this.num++;
        this.cNum = this.num;
      },
      addTrolley(){
        if(this.cNum == ''){
          this.showPop();
        }else{
          if(this.reqing){
            return false;
          }
          this.reqing = true;
          let info = JSON.stringify({
            gid: this.gId,
            num: this.num
          })
          let obj = this.doQs({
            time: TIMER,
            uid: loginInfor.uid,
            hashid: loginInfor.hashid,
            info: info,
            hash: '5154084e54c070b729fb1417b517a7c8'
          });
          this.$axios.post(APIURL + 'Api/Cart/addCart',obj).then((res)=>{
            let $this = this;
            this.webTip(res.data.Msg,function(){
              $this.reqing = false;
            })
          })
          this.hidePop();
        }
      },
      buy(){
        if(this.cNum == ''){
          this.showPop();
          return false;
        }
        let info = JSON.stringify({
          gid: this.gId,
          num: this.num
        })
        let obj = this.doQs({
          time: TIMER,
          uid: loginInfor.uid,
          hashid: loginInfor.hashid,
          type: 1,
          info: info,
          cartId:'',
          hash: '27e20c982aa6c7724ce00617bf5b174c'
        });
        this.$axios.post(APIURL + 'Api/Order/order',obj).then((res)=>{
          console.log(res);
          if(res.data.Code == '0'){
            let infor = JSON.stringify(res.data.Data.goodsInfo);
            let total = res.data.Data.totalPrice;
            console.log(infor);
            this.$router.push('/account?infor='+infor+'&total='+total+'&type=1');
          }
        })
      }
    }
  }
</script>
<style>
  .detai-header{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 1rem;
    background: #fff;
    line-height: 1rem;
    text-align: center;
    font-size: 16px;
    z-index: 110;
  }
  .detai-header .d-back{
    position: absolute;
    width: 0.5rem;
    height: 0.5rem;
    padding: 0.25rem 0.3rem;
    left: 0;
    top: 0;
  }
  .detai-header .d-back img{
    width: 100%;
    height: 100%;
  }
  .detai-header .h-right{
    position: absolute;
    width: 0.5rem;
    height: 0.5rem;
    padding: 0.25rem 0.3rem;
    right: 0;
    top: 0;
  }
  .detai-header .h-right img{
    width: 100%;
    height: 100%;
  }
  .detail-contain{
    padding-top: 1rem;
  }
  .detail-banner{
    box-shadow: 0 0 1px #ddd;
  }
  .detail-banner img{
    width: 100%;
    height: 4.8rem;
  }
  .detail-banner .swiper-pagination-bullets{
    position: absolute;
    left: 0;
    bottom: 15px;
    width: 100%;
    height: 8px;
    z-index: 20;
    text-align: center;
  }
  .d-price{
    line-height: 0.5rem;
    font-size: 16px;
    color: #fe0000;
    background: #fff;
    padding: 0.2rem 0.2rem 0 0.3rem;
  }
  .d-price em{
    font-size: 12px;
    color: #999;
    margin-left: 0.1rem;
  }
  .d-price em i{
    text-decoration: line-through;
  }
  .d-title{
    background: #fff;
    line-height: 0.5rem;
    font-size: 15px;
    font-weight: 500;
    padding:0 0.3rem 0.2rem 0.3rem;
  }
  .number{
    padding: 0.15rem 0.2rem 0.15rem 0.3rem;
    line-height: 0.5rem;
    margin-top: 0.1rem;
    background: #fff;
  }
  .number .right{
    float: right;
    text-align: right;
    font-size: 0;
  }
  .number .right img{
    width: 0.44rem;
    height: 0.44rem;
    vertical-align: middle;
  }
  .number .right .text{
    display: inline-block;
    vertical-align: middle;
    font-size: 14px;
    color: #999;
  }
  .detail-con{
    margin-top: 0.1rem;
    background: #fff;
    padding: 0.3rem;
    padding-bottom: 1.2rem;
  }
  .detail-con .title{
    line-height: 0.4rem;
  }
  .detail-con .title .die{
    height: 0.3rem;
    display: inline-block;
    vertical-align: middle;
  }
  .detail-con .title span{
    vertical-align: middle;
    font-weight: 500;
    margin-left: 0.1rem;
  }
  .intro{
    margin-top: 0.4rem;
  }
  .intro .i-tiem{
    display: flex;
    line-height: 0.6rem;
    padding-top: 0.1rem;
    border-bottom: dashed 1px #ddd;
  }
  .intro .i-tiem .t1{
    color: #999;
    flex: 0 0 3rem;
  }
  .intro .i-tiem .t2{
    flex: 1;
  }
  .detail-con h3{
    font-size: 18px;
    font-weight: 500;
    line-height: 1rem;
    text-align: center;
    font-weight: 500;
    margin-top: 0.5rem;
  }
  .d-imgs img{
    width: 100%;
  }
  .detail-bottom{
    position: fixed;
    left: 0.3rem;
    right: 0.3rem;
    bottom: 0.1rem;
    height: 1rem;
    display: flex;
    z-index: 99;
  }
  .detail-bottom span{
    flex: 1;
    text-align: center;
    line-height: 1rem;
    color: #fff;
    font-size: 16px;
  }
  .detail-bottom .add-t{
    background: #abdb4f;
    border-top-left-radius: 0.5rem;
    border-bottom-left-radius: 0.5rem;
  }
  .detail-bottom .buy-now{
    background: #98c347;
    border-top-right-radius: 0.5rem;
    border-bottom-right-radius: 0.5rem;
  }
  .detail-pop{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 88;
  }
  .detail-pop .bg{
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.6);
  }
  .detail-pop .bg.fade-enter-active,.detail-pop .bg.fade-leave-active{
    transition: all 0.3s;
  }
  .detail-pop .bg.fade-enter,.detail-pop .bg.fade-leave-active{
    opacity: 0;
  }
  .pop-con{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 5rem;
    background: #fff;
    padding: 0.3rem;
    box-sizing: border-box;
  }
  .pop-con.move-enter-active,.pop-con.move-leave-active{
    transition: all 0.3s;
  }
  .pop-con.move-enter,.pop-con.move-leave-active{
    transform: translateY(120%);
  }
  .pop-infor{
    padding: 0 0 0.3rem 2.2rem;
    position: relative;
  }
  .pop-infor:after{
    position: absolute;
    left: 0;
    bottom: -1px;
    width: 100%;
    height: 1px;
    border-top: solid 1px #ddd;
    transform: scaleY(0.5);
    content: '';
  }
  .pop-infor .img{
    position: absolute;
    left: 0;
    top: -0.8rem;
    width: 2rem;
    height: 2rem;
    background: #fff;
    box-shadow: 0 0 2px #999;
  }
  .pop-infor .img img{
    width: 100%;
    height: 100%;
  }
  .pop-infor .price{
    font-size: 16px;
    color: #fe0000;
    line-height: 0.5rem;
    padding-top: 0.2rem;
  }
  .pop-infor .price em{
    font-size: 12px;
    color: #999;
    margin-left: 0.1rem;
  }
  .pop-infor .price em i{
    text-decoration: line-through;
  }
  .pop-infor h3{
    font-size: 16px;
    line-height: 0.5rem;
  }
  .pop-num{
    line-height: 0.6rem;
    padding: 0.1rem 0;
    margin-top: 0.5rem;
  }
  .pop-num .right{
    float: right;
    font-size: 0;
    margin-right: 0.2rem;
  }
  .pop-num .right span{
    display: inline-block;
    line-height: 0.6rem;
    background: #f7f7f7;
    text-align: center;
    width: 0.6rem;
    font-size: 15px;
    color: #999;
    font-weight: 500;
  }
  .pop-num .right .num{
    margin: 0 0.1rem;
    color: #000;
  }
  .d-content p img{
    width: 100%;
  }
</style>


