<template>
  <div>
    <v-header :title="'立刻购买'"></v-header>
    <div class="a-contain">
      <div class="a-infor">
        <div class="p">
          <span>收货人</span>
          <em><input type="text" placeholder="姓名" v-model="name"></em>
          <em><input type="tel" placeholder="电话" v-model="tel"></em>
        </div>
        <div class="shop">
          <div class="right" @click="showPop">
            <div class="text"><b>{{text}}</b></div>
            <img src="../../assets/next.png" alt="">
          </div>
          <i>*</i>选择提货商家
        </div>
      </div>
      <ul class="g-list">
        <li v-for="item in infor">
          <div class="con">
            <img :src="item.goodsImg" alt="" class="g-img">
            <h3>{{item.goodsName}}</h3>
            <p>
              <span>￥{{item.price}}</span>
              ×{{item.orderNum}}
            </p>
          </div>
        </li>
      </ul>
    </div>
    <div class="bottom">
      <div class="go-pay" @click="submit">提交订单</div>
      <div class="total">
        总计：<em>￥{{total}}</em>
      </div>
    </div>
    <transition name="move">
    <div class="pop" v-show="popShow">
      <div class="pop-header">
        <div class="pop-back" @click="hidePop">
          <img src="../../assets/back1.png" alt="">
        </div>
        选择提货商家
      </div>
      <ul class="shop-list">
        <li v-for="item in shopList"  @click="chose(item.id,item.address)">
          <h4>{{item.shop_name}}</h4>
          <p>{{item.address}}</p>
        </li>
      </ul>
    </div>
    </transition>
    <transition name="fade">
    <div class="pop-bg" v-show="popShow"></div>
    </transition>
  </div>
</template>
<script>
  import header from '@/components/header/header'
  export default{
    components:{
      'v-header': header
    },
    data(){
      return {
        popShow: false,
        infor:[],
        name: '',
        tel: '',
        total: '',
        sId: '',
        shopList:[],
        text:'',
        shopId: '',
        type: ''
      }
    },
    created(){
      let i = this.$route.query.infor;
      this.total = this.$route.query.total;
      this.infor = JSON.parse(i);
      this.type = this.$route.query.type;
      console.log(this.infor);
      let obj = this.doQs({
        time: TIMER,
        hash: 'ba83adc9ac87be55baf2966d4f5408cd',
        page: 1
      });
      this.$axios.post(APIURL + 'Api/Center/addressList',obj).then((res)=>{
        console.log(res)
        if(res.data.Code == '0'){
          this.shopList = res.data.Data.list;
        }else{
          webTip(res.data.Msg)
        }
        console.log(this.shopList)
      })
    },
    methods:{
      showPop(){
        this.popShow = true;
      },
      hidePop(){
        this.popShow = false;
      },
      chose(id,address){
        this.hidePop();
        this.shopId = id;
        this.text = address;
      },
      submit(){
        let telReg = /^1([3-9][0-9])\d{8}$/;
        if(this.name == ''){
          this.webTip('请输入姓名');
          return false;
        }
        if(this.tel == ''){
          this.webTip('请输入电话');
          return false;
        }
        if(!telReg.test(this.tel)){
          this.webTip('电话号码错误');
          return false;
        }
        if(this.shopId == ''){
          this.webTip('请选择提货商家');
          return false;
        }
        var info = [];
        for(let i in this.infor){
          let a = {
            gid: this.infor[i].goodsId,
            num: this.infor[i].orderNum
          }
          info[i] = a;
        }
        
        info = JSON.stringify(info);
        let obj = this.doQs({
          time: TIMER,
          uid: loginInfor.uid,
          hashid: loginInfor.hashid,
          type: this.type,
          rname: this.name,
          phone: this.tel,
          info: info,
          shopId: this.shopId,
          hash: '553abe86d8c884a1f82f21e35d1fb7ec'
        });
        this.$axios.post(APIURL + 'Api/Order/orderSubmit',obj).then((res)=>{
          console.log(res)
          if(res.data.Code == '0'){
            this.$router.push('/pay?money='+ res.data.Data.money+'&id='+res.data.Data.oid);
          }else{
            webTip(res.data.Msg)
          }
        })
      }
    }
  }
</script>
<style scoped>
  .a-contain{
    padding: 1rem 0;
  }
  .a-infor .p{
    padding: 0.3rem;
    background: #fff;
    color: #999;
  }
  .a-infor .p span{
    vertical-align: middle;
  }
  .a-infor .p em{
    display: inline-block;
    width: 2.4rem;
    height: 0.5rem;
    border: solid 1px #ddd;
    border-radius: 3px;
    padding: 0 0.2rem;
    margin-left: 0.1rem;
    box-sizing: border-box;
    vertical-align: middle;
    color: #000;
  }
  .a-infor .p em input{
    width: 100%;
    height: 100%;
    border: none;
    padding: 0;
    margin: 0;
    font-size: 14px;
  }
  .a-infor .shop{
    padding: 0.2rem 0.3rem;
    position: relative;
    line-height: 0.6rem;
    background: #fff;
  }
  .a-infor .shop:after{
    position: absolute;
    left: 0;
    top: -1px;
    width: 100%;
    height: 1px;
    border-top:solid 1px #ddd;
    transform: scaleY(0.5);
    content: '';
  }
  .a-infor .shop .right{
    float: right;
    width: 4rem;
    text-align: right;
    height: 0.6rem;
    line-height: 0.3rem;
    overflow: hidden;
    position: relative;
    padding-right: 0.15rem;
    font-size: 12px;
    color: #999;
  }
  .a-infor .shop .right img{
    width: 0.44rem;
    height: 0.44rem;
    position: absolute;
    top: 0.08rem;
    right: -0.1rem;
  }
  .a-infor .shop .right .text{
    display: inline-block;
    vertical-align: middle;
    margin-right: 0.1rem;
  }
  .a-infor .shop .right .text b{
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    transform: translateY(-50%);
    padding-right: 0.3rem;
    box-sizing: border-box;
  }
  .a-infor .shop i{
    color: #e00000;
  }
  .g-list{
    margin-top: 0.1rem;
  }
  .g-list li{
    padding: 0.3rem;
    position: relative;
    height: 1.6rem;
    display: flex;
    background: #fff;
  }
  .g-list li:after{
    position: absolute;
    left: 0;
    bottom: -1px;
    width: 100%;
    height: 1px;
    border-top:solid 1px #eee;
    transform: scaleY(0.5);
    content: '';
  }
  .g-list li .left{
    flex: 0 0 0.6rem;
    height: 100%;
    position: relative;
  }
  .g-list li .con{
    flex: 1;
    height: 100%;
    position: relative;
    padding-left: 1.8rem;
  }
  .g-list li .left .r{
    width: 0.36rem;
    height: 0.36rem;
    border: solid 1px #ddd;
    border-radius: 50%;
    box-sizing: border-box;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
  }
  .g-list li .left .r img{
    position: absolute;
    left: -1px;
    top: -1px;
    width: 0.36rem;
    height: 0.36rem;
    display: none;
  }
  .g-list li .con .g-img{
    position: absolute;
    left: 0;
    top: 0;
    width: 1.6rem;
    height: 1.6rem;
  }
  .g-list li .con h3{
    font-size: 15px;
    line-height: 0.4rem;
    height: 0.8rem;
    margin-top: 0.1rem;
    overflow: hidden;
  }
  .g-list li .con p{
    font-size: 14px;
    color: #999;
    margin-top: 0.2rem;
  }
  .g-list li .con p span{
    color: #d30000;
    float: right;
    font-weight: 500;
  }
  .bottom{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1rem;
    z-index: 88;
    background: #fff;
    box-shadow: 0 0 1px #ddd;
    line-height: 1rem;
    padding: 0 0.3rem;
    color: #999;
    box-sizing: border-box;
  }
  .bottom .go-pay{
    position: absolute;
    right: 0;
    top: 0;
    width: 2rem;
    text-align: center;
    line-height: 1rem;
    height: 1rem;
    background: #98c347;
    background: linear-gradient(left, #aedf50, #98c347); 
    color: #fff;
    font-size: 16px;
  }
  .bottom .total{
    height: 1rem;
    color: #000;
  }
  .bottom .total em{
    color: #e00000;
    font-weight: 500;
    font-size: 16px;
  }
  .pop{
    position: fixed;
    left: 0;
    top: 0;
    background: #f7f7f7;
    width: 100%;
    height: 100%;
    z-index: 200;
  }
  .pop.move-enter-active,.pop.move-leave-active{
    transition: all 0.3s;
  }
  .pop.move-enter,.pop.move-leave-active{
    transform: translateX(100%);
  }
  .pop-bg.fade-enter-active,.pop-bg.fade-leave-active{
    transition: all 0.3s;
  }
  .pop-bg.fade-enter,.pop-bg.fade-leave-active{
    opacity: 0;
  }
  .pop-header{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 0.9rem;
    background: #fff;
    z-index: 99;
    text-align: center;
    line-height: 0.9rem;
    font-size: 16px;
    box-shadow: 0 0 1px #ddd;
  }
  .pop-header .pop-back{
    position: absolute;
    left: 0;
    top: 0;
    width: 0.44rem;
    height: 0.44rem;
    padding: 0.23rem;
  }
  .pop-header .pop-back>img{
    width: 100%;
    height: 100%;
  }
  .shop-list{
    position: absolute;
    left: 0;
    right: 0;
    top: 1rem;
    bottom: 0;
    overflow-y: auto; 
  }
  .shop-list li{
    position: relative;
    padding: 0.2rem 0.3rem;
    background: #fff;
  }
  .shop-list li:after{
    position: absolute;
    left: 0;
    bottom: -1px;
    width: 100%;
    height: 1px;
    border-top:solid 1px #eee;
    transform: scaleY(0.5);
    content: '';
  }
  .shop-list li h4{
    font-size: 15px;
    font-weight: 500;
    color: #000;
  }
  .shop-list li p{
    text-align: justify;
  }
  .pop-bg{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
    z-index: 120;
  }
</style>


