<template>
  <div class="contain">
    <div class="index-header">
      <div class="box">
        <router-link to="/msg" class="to-msg">
          <img src="./m.png" alt="">
        </router-link>
        <img src="./a.png" alt="" class="ad-dot">
        <span v-if="position">{{position.city}}{{position.district}}</span>
      </div>
      <div class="search-box">
        <form action="" ref="form" @submit="search">
          <input type="search" class="s-input" placeholder="输入关键词搜索您想要的商品" v-model="key">
        </form>
        <img src="./search.png" alt="" class="s-dot">
      </div>
    </div>
    <swiper class="index-banner" :options="swiperOption" ref="mySwiper">
      <swiper-slide v-for="(item,index) in banner" :key="index">
        <img :src="item.picture" alt="">
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
    <div class="goods-list">
      <div class="title">
        <img src="../../assets/die.png" alt="" class="die">
        <span>产品展示</span>
      </div>
      <div class="lists">
        <router-link v-for="item in goods" :to="'/detail?id='+item.id" :key="item.id">
          <img :src="item.goodsimg" alt="">
          <h3>{{item.goodsname}}</h3>
          <p>￥<b>{{item.goodsprice}}</b></p>
        </router-link>
      </div>
    </div>
    <v-nav></v-nav>
    <iframe id="geoPage" width=0 height=0 frameborder=0  style="display:none;" scrolling="no"
        src="https://apis.map.qq.com/tools/geolocation?key=OB4BZ-D4W3U-B7VVO-4PJWW-6TKDJ-WPB77&referer=myapp">
    </iframe>
  </div>
</template>
<script>
  import {swiper,swiperSlide} from 'vue-awesome-swiper'
  import nav from '@/components/nav/nav'
  export default{
    components:{
      'swiper': swiper,
      'swiperSlide': swiperSlide,
      'v-nav': nav
    },
    data(){
      return {
        swiperOption:{
          notNextTick: true,
          autoplay: {
            delay: 3000,
            stopOnLastSlide: false,
            disableOnInteraction: false,
          },
          pagination: {
            el: '.swiper-pagination',
            bulletClass : 'my-bullet',
            bulletActiveClass: 'my-bullet-active'
          },
          loop: true,
          initialSlide: 0,
        },
        key: '',
        address: {},
        banner:[],
        goods: [],
        openId:'',
        wxInfor:{},
        position:{}
      }
    },
    created(){
      let opid = localStorage.getItem('liOpenId');
      if(opid){
        this.openId = opid;
      }
      let obj = this.doQs({
        time: TIMER,
        hash: '70ffcb2a3569065c4420776b8a81809b'
      });
      this.$axios.post(APIURL + 'Api/Index/index',obj).then((res)=>{
        console.log(res)
        if(res.data.Code == '0'){
          this.banner = res.data.Data.carousel;
          this.goods = res.data.Data.goods;
        }else{
          webTip(res.data.Msg)
        }
        console.log(this.goods)
      })
    },
    mounted(){
      let $this = this;
      window.addEventListener('message', function(event) {
          // 接收位置信息
          var loc = event.data;
          if(loc){
            POSITION = loc;
            $this.position = loc;
            console.log(POSITION);
          }
      }, false);
    },
    methods:{
      search(){
        if(this.key){
          this.$router.push('/search?key='+this.key);
        }else{
          this.$router.push('/search');
        } 
      }
    }
  }
</script>
<style scoped>
.index-header{
  padding: 0.2rem 0.3rem 0.15rem 0.3rem;
  background: #a4d24c;
  background: linear-gradient(left, #aedf50, #98c347); 
}
.index-header .box{
  padding: 0.1rem 0;
  color: #fff;

}
.index-header .box .to-msg{
  float: right;
  width: 0.44rem;
  height: 0.44rem;
  padding: 0 0.1rem;
}
.index-header .box .to-msg img{
  width: 100%;
  height: 100%;
}
.index-header .box .ad-dot{
  height: 0.44rem;
  vertical-align: middle;
  margin-top: -1px;
}
.index-header .box span{
  vertical-align: middle;
  line-height: 0.44rem;
}
.search-box{
  margin-top: 0.1rem;
  position: relative;
}
.search-box input{
  appearance: none;
  border: none;
  margin: 0;
  padding: 0;
  width: 100%;
  line-height: 0.4rem;
  border-radius: 4px;
  padding: 0.1rem 0.2rem 0.1rem 0.6rem;
  box-sizing: border-box;
  font-size: 14px;
}
.search-box .s-dot{
  position: absolute;
  left: 0.1rem;
  top: 0.1rem;
  width: 0.4rem;
}
.index-banner{
  height: 2.8rem;
}
.index-banner .swiper-box,.index-banner img{
  width: 100%;
  height: 2.8rem;
}
.index-banner .swiper-pagination-bullets{
  position: absolute;
  left: 0;
  bottom: 15px;
  width: 100%;
  height: 8px;
  z-index: 20;
  text-align: center;
}
.goods-list{
  padding: 0.3rem;
}
.goods-list .title{
  line-height: 0.4rem;
}
.goods-list .title .die{
  height: 0.3rem;
  display: inline-block;
  vertical-align: middle;
}
.goods-list .title span{
  vertical-align: middle;
  font-weight: 500;
  margin-left: 0.1rem;
}
.lists{
  font-size: 0;
}
.lists a{
  display: inline-block;
  font-size: 15px;
  width: 3.3rem;
  height: 4.2rem;
  background: #fff;
  margin: 0.2rem 0.25rem 0 0;
  text-align: center;
}
.lists a:nth-child(2n){
  margin-right: 0;
}
.lists a img{
  width: 100%;
  height: 2.7rem;
}
.lists a h3{
  font-size: 14px;
  font-weight: 500;
  color: #000;
  margin-top: 0.1rem;
  line-height: 0.4rem;
  height: 0.8rem;
  overflow: hidden;
}
.lists a p{
  font-size: 12px;
  color: #98c347;
  margin-top: 0.05rem;
}
.lists a p b{
  font-size: 15px;
}
</style>


