<template>
  <div>
    <div class="c-header">分类</div>
    <v-nav></v-nav>
    <div class="classify-contain">
      <ul class="menu">
        <scroller>
          <li :class="{on: menuTab == 0}" @click="getGood(0)">为你推荐</li>
          <li v-for="(item,index) in menus" @click="getGood(item.id,index)" :class="{on: menuTab == (index+1)}">
            {{item.name}}
          </li>
        </scroller>
      </ul>
      <div class="classify-con">
        <scroller>
          <ul class="list">
            <li v-for="item in list">
              <router-link :to="'/detail?id='+item.id">
                <img :src="item.goodsimg" alt="">
                <p>{{item.goodsname}}</p>
              </router-link>
            </li>
          </ul>
        </scroller>
      </div>
    </div>
  </div>
</template>
<script>
  import nav from '@/components/nav/nav'
  export default{
    components:{
      'v-nav': nav
    },
    data() {
      return {
        menus:[],
        list:[],
        menuTab: 0
      }
    },
    created(){
      //获取分类
      let obj = this.doQs({
        time: TIMER,
        hash: '70ffcb2a3569065c4420776b8a81809b'
      });
      this.$axios.post(APIURL + 'Api/Shop/category',obj).then((res)=>{
        console.log(res)
        if(res.data.Code == '0'){
          this.menus = res.data.Data; 
        }else{
          this.webTip(res.data.Msg)
        }
      })
      this.getGood();
    },
    methods:{
      infinite(){

      },
      getGood(id,index){
        //获取商品
        if(id){
          this.menuTab = index+1;
        }else{
          this.menuTab = 0;
        }
        let obj1 = this.doQs({
          time: TIMER,
          cid: id,
          page: 1,
          hash: 'bc2dea0c01a584d9f26d8ea1afda8e60'
        });
        this.$axios.post(APIURL + 'Api/Shop/goodsList',obj1).then((res)=>{
          console.log(res)
          if(res.data.Code == '0'){
            this.list = res.data.Data.list; 
          }else{
            this.webTip(res.data.Msg)
          }
        })
      }
    }
  }
</script>
<style scoped>
  .c-header{
    padding: 0.45rem 0;
    background: linear-gradient(left, #aedf50, #98c347);
    line-height: 0;
    height: 0;
    font-size: 16px;
    color: #fff;
    text-align: center;
  }
  .classify-contain{
    position: absolute;
    left: 0;
    right: 0;
    top: 0.9rem;
    bottom: 1rem;
    display: flex;
  }
  .menu{
    flex: 0 0 2rem;
    height: 100%;
    background: #f7f7f7;
    box-shadow: 0 0 1px #ddd;
  }
  .classify-con{
    flex: 1;
    height: 100%;
    background: #fff;
    position: relative;
  }
  .menu li{
    width: 2rem;
    text-align: center;
    font-size: 14px;
    border-bottom: solid 1px #fff;
    height: 0;
    padding: 0.5rem 0;
    line-height: 0;
  }
  .menu li:last-child{
    border: none;
  }
  .menu li.on{
    background: #fff;
    color: #abdb4f;
    border-color: #abdb4f;
    font-weight: 500;
  }
  .list{
    font-size: 0;
    padding: 0.2rem;
  } 
  .list li{
    display: inline-block;
    font-size: 13px;
    width: 2.4rem;
    margin-right: 0.2rem;
    text-align: left;
    height: 2.6rem;
    overflow-y: hidden;
  }
  .list li:nth-child(2n){
    margin-right: 0;
  }
  .list li img{
    width: 100%;
    height: 1.6rem;
  }
  .list li p{
    margin-top: 0.1rem;
    line-height: 0.4rem;
    height: 0.8rem;
    overflow-y: hidden;
    text-align: justify;
  }
  .list li a{
    display: block;
  }
</style>


