<template>
  <div>
    <v-header :title="'购物车'"></v-header>
    <div class="bottom">
      <div class="r" :class="{checked: allChecked}" @click="checkAll">
        <img src="../../assets/checked.png" alt="">
      </div>
      <span>全选</span>
      <div class="go-pay" @click="goPay">去支付</div>
      <div class="total">
        总计：<em>￥{{total}}</em>
      </div>
    </div>
    <div class="trolley-contain">
      <ul class="g-list">
        <li v-for="(item,index) in list" :class="{editing:editing}">
          <div class="left" @click="check(index)">
            <div class="r" :class="{checked: item.checked}">
              <img src="../../assets/checked.png" alt="">
            </div>
          </div>
          <div class="con">
            <img :src="item.goodsimg" alt="" class="g-img">
            <h3>{{item.goodsname}}</h3>
            <p>
              <span>￥{{item.goodsprice}}</span>
              <b v-show="!editing">×{{item.gnum}}</b>
              <div class="do" v-show="editing">
                <i @click="randuce(index)">-</i>
                <i class="num">{{item.gnum}}</i>
                <i @click="add(index)">+</i>
              </div>
            </p>
          </div>
          <div class="del" @click="del(index)">
            <div class="text">删除</div>
          </div>
        </li>
      </ul>
      <!-- <div class="shop-item">
        <div class="title">
          <img src="./e.png" alt="" class="edit">
          编号
        </div>
        <ul class="g-list">
          <li>
            <div class="left">
              <div class="r">
                <img src="../../assets/checked.png" alt="">
              </div>
            </div>
            <div class="con">
              <img src="../../assets/g2.png" alt="" class="g-img">
              <h3>名称</h3>
              <p>
                <span>￥260.00</span>
                ×1
              </p>
            </div>
          </li>
          <li>
            <div class="left">
              <div class="r">
                <img src="../../assets/checked.png" alt="">
              </div>
            </div>
            <div class="con">
              <img src="../../assets/g2.png" alt="" class="g-img">
              <h3>名称</h3>
              <p>
                <span>￥260.00</span>
                ×1
              </p>
            </div>
          </li>
        </ul>
      </div> -->
      <div class="no-data" v-show="noData">您的购物车暂无商品</div>
    </div>
    <img src="./e.png" alt="" class="edit" v-if="!editing" @click="edit">
    <div class="finish" v-else @click="finish">完成</div>
  </div>
</template>
<script>
  import header from '@/components/header/header'
  export default{
    components:{
      'v-header': header
    },
    data(){
      return {
        list:[],
        noData: false,
        allChecked: false,
        total: '0.00',
        editing: false
      }
    },
    created(){
      this.getData();
    },
    methods:{
      check(index){
        this.list[index].checked = !this.list[index].checked;
        let length = this.list.length;
        let n = 0;
        for(let i in this.list){
          if(this.list[i].checked){
            n++
          }
        }
        if(n == length){
          this.allChecked = true;
        }else{
          this.allChecked = false;
        }
        this.getMoney();
      },
      checkAll(){
        if(this.allChecked){
          this.allChecked = false;
          for(let i in this.list){
            this.list[i].checked = false;
          }
        }else{
          this.allChecked = true;
          for(let i in this.list){
            this.list[i].checked = true;
          }
        }
        this.getMoney();
      },
      getMoney(){
        let num = 0;
        for(let i in this.list){
          if(this.list[i].checked){
            let n = this.list[i].gnum;
            let p = this.list[i].goodsprice*100;
            let m = n*p;
            num = num+m;
          }
        }
        this.total = parseFloat(num/100).toFixed(2);
      },
      edit(){
        this.editing = true;
      },
      finish(){
        this.editing = false;
      },
      randuce(index){
        let num = this.list[index].gnum;
        let cid = this.list[index].id
        if(num == 1){
          return false;
        }
        num--;
        this.list[index].gnum = num;
        this.getMoney();
        let obj = this.doQs({
          time: TIMER,
          uid: loginInfor.uid,
          hashid: loginInfor.hashid,
          type: 1,
          cartId: cid,
          num: num,
          hash: 'f848d1aebebbb31640f5dd9cfd8a3258'
        });
        this.$axios.post(APIURL + 'Api/Cart/editCart',obj).then((res)=>{
          console.log(res)
        })
      },
      add(index){
        let num = this.list[index].gnum;
        let cid = this.list[index].id
        num++;
        this.list[index].gnum = num;
        this.getMoney();
        let obj = this.doQs({
          time: TIMER,
          uid: loginInfor.uid,
          hashid: loginInfor.hashid,
          type: 1,
          cartId: cid,
          num: num,
          hash: 'f848d1aebebbb31640f5dd9cfd8a3258'
        });
        this.$axios.post(APIURL + 'Api/Cart/editCart',obj).then((res)=>{
          console.log(res)
        })
      },
      del(index){
        let num = this.list[index].gnum;
        let cid = this.list[index].id
        let obj = this.doQs({
          time: TIMER,
          uid: loginInfor.uid,
          hashid: loginInfor.hashid,
          type: 2,
          cartId: cid,
          num: num,
          hash: 'f848d1aebebbb31640f5dd9cfd8a3258'
        });
        this.$axios.post(APIURL + 'Api/Cart/editCart',obj).then((res)=>{
          this.getData();
        })
      },
      getData(){
        let obj = this.doQs({
          time: TIMER,
          uid: loginInfor.uid,
          hashid: loginInfor.hashid,
          hash: '1522546fb8e6c8b6c2d37837e99d0730',
          page: 1
        });
        this.$axios.post(APIURL + 'Api/Cart/cartList',obj).then((res)=>{
          console.log(res)
          if(res.data.Code == 0){
            let data = res.data.Data.list;
            if(data.length>0){
              this.noData = false;
              for(let i in data){
                data[i].checked = false;
              }
              this.list = data;
              console.log(this.list);
            }else{
              this.list = res.data.Data.list;
              this.editing = false;
              this.noData = true;
            }
          }else{
            this.webTip(res.data.Msg);
          }
          this.getMoney();
        })
      },
      goPay(){
        let n = 0;
        for(let i in this.list){
          if(this.list[i].checked){
            n++
          }
        }
        if(n == 0){
          this.webTip('请选择商品');
          return false;
        }
        let str = '';
        for(let n in this.list){
          if(this.list[n].checked){
            str = str + this.list[n].id + ',';
          }
        }
        let obj = this.doQs({
          time: TIMER,
          uid: loginInfor.uid,
          hashid: loginInfor.hashid,
          type: 2,
          info: '',
          cartId:str,
          hash: '27e20c982aa6c7724ce00617bf5b174c'
        });
        this.$axios.post(APIURL + 'Api/Order/order',obj).then((res)=>{
          console.log(res);
          if(res.data.Code == '0'){
            let infor = JSON.stringify(res.data.Data.goodsInfo);
            let total = res.data.Data.totalPrice;
            this.$router.push('/account?infor='+infor+'&total='+total+'&type=1');
          }
        })
      }
    }
  }
</script>
<style scoped>
.bottom{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1rem;
  z-index: 88;
  background: #fff;
  box-shadow: 0 0 1px #ddd;
  line-height: 1rem;
  padding: 0 0.3rem;
  color: #999;
  box-sizing: border-box;
}
.trolley-contain{
  padding: 0.9rem 0 1rem 0;
}
.shop-item{
  padding: 0 0.3rem;
  margin-bottom: 0.1rem;
  background: #fff;
}
.shop-item .title{
  padding: 0.2rem 0 0.1rem 0.6rem;
  position: relative;
  line-height: 0.5rem;
  font-size: 15px;
}
/*.shop-item .title .edit{
  position: absolute;
  right: 0;
  top: 0.2rem;
  width: 0.44rem;
  height: 0.44rem;
}*/
.g-list{
  width: 100%;
  overflow-x: hidden;
}
.g-list li{
  padding: 0.3rem 0.2rem;
  position: relative;
  height: 1.6rem;
  display: flex;
  background: #fff;
  transition: all 0.2s;
}
.g-list li:after{
  position: absolute;
  left: 0;
  top: -1px;
  width: 100%;
  height: 1px;
  border-top:solid 1px #ddd;
  transform: scaleY(0.5);
  content: '';
}
.g-list li .left{
  flex: 0 0 0.6rem;
  height: 100%;
  position: relative;
}
.g-list li .con{
  flex: 1;
  height: 100%;
  position: relative;
  padding-left: 1.8rem;
}
.g-list li .left .r{
  width: 0.36rem;
  height: 0.36rem;
  border: solid 1px #ddd;
  border-radius: 50%;
  box-sizing: border-box;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
}
.g-list li .left .r img{
  position: absolute;
  left: -1px;
  top: -1px;
  width: 0.36rem;
  height: 0.36rem;
  display: none;
}
.g-list li .left .checked img{
  display: block;
}
.g-list li .con .g-img{
  position: absolute;
  left: 0;
  top: 0;
  width: 1.6rem;
  height: 1.6rem;
}
.g-list li .con h3{
  font-size: 15px;
  line-height: 0.4rem;
  height: 0.8rem;
  margin-top: 0.1rem;
  overflow: hidden;
  text-align: justify;
}
.g-list li .con p{
  font-size: 14px;
  color: #999;
  line-height: 0.5rem;
}
.g-list li .con p span{
  color: #d30000;
  float: right;
  font-weight: 500;
}
.g-list li.editing{
  transform: translateX(-1.6rem);
}
.g-list li .del{
  position: absolute;
  top: 0;
  right: -1.6rem;
  width: 1.6rem;
  background: #d30000;
  color: #fff;
  height: 100%;
  text-align: center;
  font-size: 15px;
}
.g-list li .del:before{
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  border-bottom: solid 1px #ddd;
  transform: scaleY(0.5);
  content: '';
  z-index: 99;
}
.g-list li:last-child .del:before{
  border: none;
}
.g-list li .del .text{
  line-height: 0.5rem;
  position: absolute;
  top: 50%;
  width: 100%;
  margin-top: -0.25rem;
}
.bottom .r{
  display: inline-block;
  width: 0.4rem;
  height: 0.4rem;
  border-radius: 50%;
  border: solid 1px #ddd;
  vertical-align: middle;
  position: relative;
  box-sizing: border-box;
}
.bottom .r img{
  position: absolute;
  left: -1px;
  top: -1px;
  width: 0.4rem;
  height: 0.4rem;
  display: none;
}
.bottom .checked img{
  display: block;
}
.bottom span{
  vertical-align: middle;
  margin-left: 0.1rem;
}
.bottom .go-pay{
  position: absolute;
  right: 0;
  top: 0;
  width: 2rem;
  text-align: center;
  line-height: 1rem;
  height: 1rem;
  background: #98c347;
  background: linear-gradient(left, #aedf50, #98c347); 
  color: #fff;
  font-size: 16px;
}
.bottom .total{
  position: absolute;
  right: 2.2rem;
  top: 0;
  width: 3rem;
  text-align: right;
  height: 1rem;
  color: #000;
}
.bottom .total em{
  color: #e00000;
  font-weight: 500;
  font-size: 16px;
}
.edit{
  position: fixed;
  right: 0;
  top: 0;
  width: 0.5rem;
  height: 0.5rem;
  padding: 0.2rem;
  z-index: 100;
}
.finish{
  position: fixed;
  right: 0;
  top: 0;
  padding: 0.2rem;
  line-height: 0.5rem;
  font-size: 15px;
  color: #99c548;
  z-index: 110;
}
.g-list li .do{
  font-size: 0;
}
.g-list li .do i{
  display: inline-block;
  text-align: center;
  width: 0.5rem;
  height: 0;
  text-align: center;
  line-height: 0;
  font-size: 14px;
  background: #f7f7f7;
  color: #999;
  padding: 0.25rem 0;
}
.g-list li .do .num{
  margin: 0 0.1rem;
  font-size: 14px;
  color: #000;
}
.no-data{
  text-align: center;
  margin-top: 0.8rem;
  color: #999;
}
</style>


