import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index/index'
import detail from '@/components/detail/detail'
import trolley from '@/components/trolley/trolley'
import account from '@/components/account/account'
import pay from '@/components/pay/pay'
import succ from '@/components/succ/succ'
import msg from '@/components/msg/msg'
import classify from '@/components/classify/classify'
import near from '@/components/near/near'
import shop from '@/components/shop/shop'
import my from '@/components/my/my'
import edit from '@/components/edit/edit'
import order from '@/components/order/order'
import odetail from '@/components/odetail/odetail'
import feedback from '@/components/feedback/feedback'
import search from '@/components/search/search'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index',
      name: 'index',
      component: index
    },
    {
      path: '/detail',
      name: 'detail',
      component: detail
    },
    {
      path: '/trolley',
      name: 'trolley',
      component: trolley
    },
    {
      path: '/account',
      name: 'account',
      component: account
    },
    {
      path: '/pay',
      name: 'pay',
      component: pay
    },
    {
      path: '/succ',
      name: 'succ',
      component: succ
    },
    {
      path: '/msg',
      name: 'msg',
      component: msg
    },
    {
      path: '/classify',
      name: 'classify',
      component: classify
    },
    {
      path: '/near',
      name: 'near',
      component: near
    },
    {
      path: '/shop',
      name: 'shop',
      component: shop
    },
    {
      path: '/my',
      name: 'my',
      component: my
    },
    {
      path: '/edit',
      name: 'edit',
      component: edit
    },
    {
      path: '/order',
      name: 'order',
      component: order
    },
    {
      path: '/odetail',
      name: 'odetail',
      component: odetail
    },
    {
      path: '/feedback',
      name: 'feedback',
      component: feedback
    },
    {
      path: '/search',
      name: 'search',
      component: search
    },
  ],
  linkActiveClass: 'active'
})



