<template>
  <div>
    <v-header :title="'资料编辑'"></v-header>
    <div class="edit-con">
      <div class="item a">
        <div class="right">
          <div class="avatar">
            <img :src="infor.avatar" alt="">
            <!-- <input type="file" ref="file" class="file"> -->
          </div>
          <!-- <img src="../../assets/next.png" alt="" class="next"> -->
        </div>
        头像
      </div>
      <div class="item">
        <div class="right">
          <input type="text" class="input" placeholder="请输入您的用户名" v-model="infor.nickname">
        </div>
        用户名
      </div>
      <div class="item">
        <div class="right">
          <!-- <div class="text">18888888888</div> -->
          <input type="tel" class="input" placeholder="请输入您的手机号" v-model="infor.phone">
        </div>
        手机号
      </div>
      <div class="edit-btn" @click="submit">保存</div>
    </div>
  </div>
</template>
<script>
  import header from '@/components/header/header'
  export default{
    components:{
      'v-header': header
    },
    data(){
      return {
        infor:{}
      }
    },
    created(){
      let obj = this.doQs({
        time: TIMER,
        uid: loginInfor.uid,
        hashid: loginInfor.hashid,
        hash: 'e785f07736bde4d62b77d03214d29647'
      });
      this.$axios.post(APIURL + 'Api/Center/manager',obj).then((res)=>{
        console.log(res)
        this.infor = res.data.Data;
      })
    },
    methods:{
      submit(){
        let telReg = /^1([3-9][0-9])\d{8}$/;
        if(!this.infor.nickname){
          this.webTip('请输入昵称');
          return false;
        }
        if(!this.infor.phone){
          this.webTip('请输入手机号码');
          return false;
        }
        if(!telReg.test(this.infor.phone)){
          this.webTip('手机号码不正确');
          return false;
        }
        let obj = this.doQs({
          time: TIMER,
          uid: loginInfor.uid,
          hashid: loginInfor.hashid,
          nickname: this.infor.nickname,
          phone: this.infor.phone,
          hash: '148e2b38832475a71035754aee707be8'
        });
        this.$axios.post(APIURL + 'Api/Center/managerSubmit',obj).then((res)=>{
          console.log(res)
          if(res.data.Code == 0){
            let $this = this;
            this.webTip('更新成功',function(){
              $this.$router.go(-1);
            })
          }
        })
      }
    }
  }
</script>
<style scoped>
.edit-con{
  padding-top: 1rem;
}
.edit-con .item{
  background: #fff;
  padding: 0.2rem 0.3rem;
  position: relative;
  font-size: 15px;
  height: 0.5rem;
  line-height: 0.5rem;
}
.edit-con .item.a{
  height: 0.8rem;
  line-height: 0.8rem;
}
.edit-con .item:after{
  position: absolute;
  left: 0;
  bottom: -1px;
  width: 100%;
  height: 1px;
  border-top:solid 1px #ddd;
  transform: scaleY(0.5);
  content: '';
}
.edit-con .item .right{
  float: right;
  width: 4rem;
  text-align: right;
  font-size: 0;
}
.edit-con .item .right .avatar{
  display: inline-block;
  width: 0.8rem;
  height: 0.8rem;
  position: relative;
  vertical-align: middle;
}
.edit-con .item .right .avatar img{
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.edit-con .item .right .avatar .file{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 20;
  opacity: 0;
}
.edit-con .item .right .next{
  display: inline-block;
  width: 0.44rem;
  height: 0.44rem;
  vertical-align: middle;
  margin-left: 0.1rem;
}
.edit-con .item .right .input{
  width: 3rem;
  display: inline-block;
  margin: 0;
  padding: 0;
  line-height: 0.5rem;
  text-align: right;
  border: none;
  font-size: 14px;
  color: #333;
}
.edit-con .item .right .text{
  font-size: 14px;
  text-align: right;
  color: #333;
}
.edit-btn{
  margin: 0.8rem 0.3rem 0 0.3rem;
  height: 0;
  padding: 0.45rem 0;
  text-align: center;
  font-size: 18px;
  color: #fff;
  background: linear-gradient(left, #aedf50, #98c347);
  line-height: 0;
  border-radius: 0.45rem;
}
</style>


