<template>
  <div class="s-header">
    <div class="back" @click="back">
      <img src="../../assets/back1.png" alt="">
    </div>
    {{title}}
  </div>
</template>
<script>
  export default{
    props:{
      title: String
    },
    methods:{
      back(){
        this.$router.go(-1);
      }
    }
  }
</script>
<style scoped>
.s-header{
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 0;
  background: #fff;
  z-index: 99;
  text-align: center;
  line-height: 0;
  font-size: 16px;
  box-shadow: 0 0 1px #ddd;
  padding: 0.45rem 0;
  font-weight: 500;
}
.s-header .back{
  position: absolute;
  left: 0;
  top: 0;
  width: 0.44rem;
  height: 0.44rem;
  padding: 0.23rem;
}
.s-header .back>img{
  width: 100%;
  height: 100%;
}
</style>


