<template>
  <div class="white">
    <img src="./succ.png" alt="" class="img">
    <p class="tt">恭喜您！支付完成</p>
  </div>
</template>
<script>
  export default{
    created(){
      var $this = this;
      setTimeout(function(){
        $this.$router.push('/index');
      },3000)
    }
  }
</script>
<style scoped>
  .white{
    background: #fff;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
  }
  .img{
    width: 100%;
  }
  .tt{
    text-align: center;
    margin-top: 1.2rem;
    font-size: 18px;
  }
</style>


