<template>
  <div>
    <v-header :title="'我的订单'"></v-header> 
    <div class="order-menu">
      <div class="item" :class="{on: tab == 0}" @click="change(0)">未完成</div>
      <div class="item" :class="{on: tab == 1}" @click="change(1)">待提取</div>
      <div class="item" :class="{on: tab == 2}" @click="change(2)">已完成</div>
    </div>
    <div class="order-contain">
      <ul class="list" v-show="tab == 0">
        <li v-for="item in list1">
            <router-link :to="'/odetail?id='+item.id">
            <div class="title">
              <div class="status">未完成</div>
              {{item.order_no}}
            </div>
            <div class="con" v-for="con in item.sublist">
              <img :src="con.gimg" alt="">
              <div class="dd">
                <h3>{{con.gname}}</h3>
                <p>
                  <span>￥{{con.price}}</span>
                  ×{{con.num}}
                </p>
              </div>
            </div>
            </router-link>
            <div class="option">
              <div class="item">
                <span class="cancel" @click="cancel(item.id)">取消订单</span>
              </div>
              <div class="item">
                <span @click="goPay(item.id)">去支付</span>
              </div>
            </div>
        </li>
      </ul>
      <ul class="list" v-show="tab == 1">
        <li v-for="item in list2">
          <router-link :to="'/odetail?id='+item.id">
            <div class="title">
              <div class="status">待提货</div>
              {{item.order_no}}
            </div>
            <div class="con" v-for="con in item.sublist">
              <img :src="con.gimg" alt="">
              <div class="dd">
                <h3>{{con.gname}}</h3>
                <p>
                  <span>￥{{con.price}}</span>
                  ×{{con.num}}
                </p>
              </div>
            </div>
            </router-link>
            <div class="option">
              <div class="item">
                <span @click="sure(item.id)">确认收货</span>
              </div>
            </div>
          
        </li>
      </ul>
      <ul class="list" v-show="tab == 2">
        <li v-for="item in list3">
          <router-link :to="'/odetail?id='+item.id">
            <div class="title">
              <div class="status">已完成</div>
              {{item.order_no}}
            </div>
            <div class="con" v-for="con in item.sublist">
              <img :src="con.gimg" alt="">
              <div class="dd">
                <h3>{{con.gname}}</h3>
                <p>
                  <span>￥{{con.price}}</span>
                  ×{{con.num}}
                </p>
              </div>
            </div>
          </router-link>
        </li>
      </ul>
    </div>  
  </div>
</template>
<script>
  import header from '@/components/header/header'
  export default{
    components:{
      'v-header': header
    },
    data() {
      return{
        tab: 0,
        type: 1,
        list1:[],
        list2:[],
        list3:[]
      }
    },
    created(){
      this.getData(1);
      this.getData(2);
      this.getData(3);
    },
    methods:{
      change(index){
        if(index == this.tab){
          return false;
        }
        this.tab = index;
      },
      getData(type){
        let obj = this.doQs({
          time: TIMER,
          uid: loginInfor.uid,
          hashid: loginInfor.hashid,
          type: type,
          page: 1,
          hash: '551d6d766a0730f6045c9766488421cd'
        });
        this.$axios.post(APIURL + 'Api/Order/orderList',obj).then((res)=>{
          console.log(res)
          if(res.data.Code == '0'){
            if(type == 1){
              this.list1 = res.data.Data.list
            }else if(type == 2){
              this.list2 = res.data.Data.list
            }else if(type == 3){
              this.list3 = res.data.Data.list
            }
          }else{
            webTip(res.data.Msg)
          }
          console.log(this.goods)
        })
      },
      cancel(id){
        let $this = this;
        let obj = this.doQs({
          time: TIMER,
          uid: loginInfor.uid,
          hashid: loginInfor.hashid,
          id: id,
          hash: '3435103f11f21993476fe72ff7a20b8f'
        });
        this.$axios.post(APIURL + 'Api/Order/orderCancel',obj).then((res)=>{
          console.log(res)
          if(res.data.Code == 0){
            this.webTip('取消成功');
            this.getData(1);
          }else{
            this.webTip(res.data.Msg);
          }
          
        })
      },
      goPay(id){
        this.$router.push('/pay?id='+id);
      },
      sure(id){
        let $this = this;
        let obj = this.doQs({
          time: TIMER,
          uid: loginInfor.uid,
          hashid: loginInfor.hashid,
          id: id,
          hash: '3435103f11f21993476fe72ff7a20b8f'
        });
        this.$axios.post(APIURL + 'Api/Order/orderConfirm',obj).then((res)=>{
          console.log(res)
          if(res.data.Code == 0){
            this.webTip('确认收货成功');
            this.getData(2);
          }else{
            this.webTip(res.data.Msg);
          }
        })
      }
    }
  }
</script>
<style scoped>
.order-menu{
  width: 100%;
  position: fixed;
  height: 0.7rem;
  background: #fff;
  left: 0;
  top: 0.9rem;
  display: flex;
  text-align: center;
  z-index: 99;
}
.order-menu .item{
  flex: 1;
  line-height: 0.7rem;
  font-size: 15px;
  color: #999;
  position: relative;
}
.order-menu .item:after{
  position: absolute;
  left: 40%;
  bottom: 0;
  width: 20%;
  height: 2px;
  background: #98c347;
  content: '';
  display: none;
}
.order-menu .item.on{
  color: #98c347;
}
.order-menu .item.on:after{
  display: block;
}
.order-contain{
  padding-top: 1.6rem;
}
.list li{
  padding: 0 0.3rem;
  background: #fff;
  position: relative;
  margin-top: 0.1rem;
}
.list li a{
  display: block;
}
.list li .title{
  line-height: 0.6rem;
  position: relative;
  color: #999;
}
.list li .title .status{
  float: right;
  color: #98c347;
}
.list li .title:after{
  position: absolute;
  left: 0;
  bottom: -1px;
  width: 100%;
  height: 1px;
  border-top:solid 1px #eee;
  transform: scaleY(0.5);
  content: '';
}
.list li .con{
  display: flex;
  padding: 0.2rem 0;
  height: 1.5rem;
  position: relative;
}
.list li .con img{
  width: 1.5rem;
  height: 1.5rem;
  flex: 0 0 1.5rem;
}
.list li .con .dd{
  flex: 1;
  padding-left: 0.2rem;
}
.list li .con .dd h3{
  line-height: 0.4rem;
  font-size: 15px;
  font-weight: 500;
  height: 0.8rem;
  margin-top: 0.1rem;
  overflow-y: hidden;
}
.list li .con .dd p{
  color: #999;
  font-size: 15px;
}
.list li .con .dd p span{
  color: #e10000;
  font-weight: 500;
  float: right;
}
.list li .option{
  position: relative;
  padding: 0.2rem 0;
  text-align: right;
  font-size: 0;
  height: 0.5rem;
}
.list li .option:after{
  position: absolute;
  left: 0;
  top: -1px;
  width: 100%;
  height: 1px;
  border-top:solid 1px #ddd;
  transform: scaleY(0.5);
  content: '';
}
.list li .option .item{
  display: inline-block;
  width: 1.5rem;
  height: 0.5rem;
  margin-left: 0.2rem;
}
.list li .option .item span{
  display: block;
  width: 200%;
  height: 200%;
  font-size: 26px;
  border: solid 1px #a0c759;
  box-sizing: border-box;
  border-radius: 0.5rem;
  transform: scale(0.5) translate(-50%,-50%);
  text-align: center;
  line-height: 1rem;
  color: #a0c759;
}
.list li .option .item span.cancel{
  color: #999;
  border-color: #999;
}
</style>


