<template>
  <div>
    <v-header :title="'消息中心'"></v-header>
    <div class="msg-contain">
      <div class="msg-list">
        <div class="item" v-for="(item,index) in list" :class="{doing:item.edit}" v-hammer:swipe.left="swipeLeft" @touchstart="start(index)">
          <h3>
            <span>{{item.create_time}}</span>
            {{item.title}}
          </h3>
          <p>{{item.description}}</p>
          <div class="del" @click="del(item.id)"><em>删除</em></div>
          <div class="cc" v-show="item.edit" v-hammer:swipe.right="swipeRight"></div>
        </div>
      </div>
      <div class="none" v-show="noData">暂无消息</div>
    </div>
  </div>
</template>
<script>
  import header from '@/components/header/header'
  export default{
    components:{
      'v-header': header
    },
    data(){
      return{
        index: 0,
        list:[],
        noData: false
      }
    },
    created(){
      let obj = this.doQs({
        time: TIMER,
        uid: loginInfor.uid,
        hashid: loginInfor.hashid,
        hash: '1522546fb8e6c8b6c2d37837e99d0730',
        page: 1
      });
      this.$axios.post(APIURL + 'Api/Index/message',obj).then((res)=>{
        console.log(res)
        if(res.data.Code == '0'){
          let mlist = res.data.Data.list;
          if(mlist.length == 0){
            this.noData = true;
          }
          for(let i in mlist){
            mlist[i].edit = false;
          }
          this.list = mlist;
          console.log(this.list)
        }else{
          webTip(res.data.Msg)
        }
      })
    },
    methods: {
      swipeLeft(){
        for(let i in this.list){
          this.list[i].edit = false;
        }
        this.list[this.index].edit= true;
      },
      swipeRight(){
        this.list[this.index].edit= false;
      },
      del(id){
        let obj = this.doQs({
          time: TIMER,
          uid: loginInfor.uid,
          hashid: loginInfor.hashid,
          hash: '3435103f11f21993476fe72ff7a20b8f',
          id: id
        });
        this.$axios.post(APIURL + 'Api/Index/messageDel',obj).then((res)=>{
          console.log(res)
          if(res.data.Code == '0'){
            this.webTip('删除成功');
            let obj1 = this.doQs({
              time: TIMER,
              uid: loginInfor.uid,
              hashid: loginInfor.hashid,
              hash: '1522546fb8e6c8b6c2d37837e99d0730',
              page: 1
            });
            this.$axios.post(APIURL + 'Api/Index/message',obj1).then((res)=>{
              console.log(res)
              if(res.data.Code == '0'){
                let mlist = res.data.Data.list;
                if(mlist.length == 0){
                  this.noData = true;
                }else{
                  this.noData = false;
                }
                for(let n in mlist){
                  mlist[n].edit = false;
                }
                this.list = mlist;
              }else{
                webTip(res.data.Msg)
              }
            })
          }else{
            webTip(res.data.Msg)
          }
        })
      },
      start(index){
        this.index = index;
      }
    }
  }
</script>
<style scoped>
  .msg-contain{
    padding-top: 0.9rem;
  }
  .msg-list{
    padding: 0 0.3rem;
  }
  .msg-list .item{
    margin-top: 0.3rem;
    background: #fff;
    box-shadow: 0 0 2px #ddd;
    border-radius: 4px;
    padding: 0.25rem 0.3rem 0.25rem 0.6rem;
    position: relative;
    transition: all 0.3s;
  }
  .msg-list .item h3{
    font-size: 15px;
    font-weight: 500;
    line-height: 0.6rem;
  }
  .msg-list .item h3 span{
    float: right;
    font-weight: normal;
    color: #999;
    font-size: 13px;
    line-height: 0.6rem;
  }
  .msg-list .item p{
    font-size: 13px;
    color: #999;
    text-align: justify;
  }
  .msg-list .doing{
    transform: translateX(-1.78rem);
  }
  .msg-list .item .del{
    position: absolute;
    right: -1.9rem;
    top: 0;
    width: 1.6rem;
    height: 100%;
    background: #d20000;
    text-align: center;
    color: #fff;
    line-height: 100%;
    border-radius: 4px;
  }
  .msg-list .item .del em{
    display: block;
    line-height: 0.5rem;
    font-size: 16px;
    position: relative;
    top: 50%;
    margin-top: -0.25rem;
  }
  .msg-list .item .cc{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
  .none{
    margin-top: 0.8rem;
    text-align: center;
    color: #999;
  }
</style>


