<template>
  <div>
    <v-nav></v-nav>
    <div class="c-header">附近</div>
    <div class="near-menu">
      <div class="item" :class="{on: dis == 2}" @click="shai">
        距离
        <img src="./sj.png" alt="">
        <img src="./sj1.png" alt="" class="on-img">
      </div>
      <div class="item" @click="showPop">
        筛选
        <img src="./sx.png" alt="">
        <img src="./sx1.png" alt="" class="on-img">
      </div>
    </div>
    <div class="near-contain">
      <ul class="shop-list">
        <scroller>
          <li v-for="item in list">
            <a href="#">
              <img :src="item.face" alt="">
              <div class="con">
                <h3>{{item.shop_name}}</h3>
                <p>距离{{item.distance}}</p>
              </div>
            </a>
          </li>
        </scroller>
      </ul>
      <div class="none" v-show="none">暂无商家</div>
    </div>
    <div class="pop" v-show="popShow">
      <transition name="fade">
      <div class="bg" v-show="conShow" @click="hidePop"></div>
      </transition>
      <transition name="move">
      <div class="pop-con" v-show="conShow">
        <div class="c-item">
          <h4>地区</h4>
          <ul class="c-list">
            <li v-for="(item,index) in area" :class="{on: item.checked}" @click="chose(index)">{{item.area}}</li>
          </ul>
        </div>
      </div>
      </transition>
    </div>
  </div>
</template>
<script>
  import nav from '@/components/nav/nav'
  export default{
    components:{
      'v-nav': nav
    },
    data(){
      return {
        popShow: false,
        conShow: false,
        list:[],
        dis: 1,
        city:'',
        county:'',
        area:[],
        none: false
      }
    },
    created(){
      this.getData();
      let obj = this.doQs({
        time: TIMER,
        hash: '38b7b05226a905ec20d3ee857ec1cede',
        city: POSITION.city
      });
      this.$axios.post(APIURL + 'Api/Center/jobCounty',obj).then((res)=>{
        console.log(res)
        if(res.data.Code == '0'){
          let data = res.data.Data;
          for(let i in data){
            data[i].checked = false;
          }
          this.area = data; 
        }else{
          this.webTip(res.data.Msg)
        }
      })
    },
    methods:{
      showPop(){
        this.popShow = true;
        this.conShow = true;
      },
      hidePop(){
        this.conShow = false;
        let $this = this;
        setTimeout(function(){
          $this.popShow = false;
        },300)
      },
      shai(){
        if(this.popShow){
          this.hidePop();
          return false;
        }
        if(this.none){
          return false;
        }
        if(this.dis == 1){
          this.dis = 2;
        }else{
          this.dis = 1;
        }
        this.getData();
      },
      chose(index){
        if(this.area[index].checked){
          return false;
        }
        for(let i in this.area){
          this.area[i].checked = false;
        }
        this.area[index].checked = true;
        this.county = this.area[index].id;
        this.dis = 1;
        this.hidePop();
        this.getData();
      },
      getData(){
        let obj = this.doQs({
          time: TIMER,
          hash: '81b54372dcc92622ad174ad0498ce697',
          page: 1,
          distance: this.dis,
          longitude: POSITION.lng,
          latitude: POSITION.lat,
          area_string: POSITION.city,
          county: this.county
        });
        this.$axios.post(APIURL + 'Api/Shop/shopList',obj).then((res)=>{
          console.log(res)
          if(res.data.Code == '0'){
            this.list = res.data.Data.list;
            if(this.list.length == 0){
              this.none = true;
            }else{
              this.none = false;
            }
          }else{
            this.webTip(res.data.Msg)
          }
        })
      }
    }
  }
</script>
<style scoped>
  .c-header{
    padding: 0.45rem 0;
    background: #98c347;
    background: linear-gradient(left, #aedf50, #98c347);
    line-height: 0;
    height: 0;
    font-size: 16px;
    color: #fff;
    text-align: center;
    z-index: 88;
    position: relative;
  }
  .near-menu{
    display: flex;
    padding: 0.15rem 0;
    background: #fff;
    box-shadow: 0 0 1px #ddd;
    line-height: 0.4rem;
    position: relative;
    z-index: 88;
  }
  .near-menu .item{
    flex: 1;
    text-align: center;
    color: #999;
  }
  .near-menu .item img{
    height: 0.3rem;
    vertical-align: middle;
  }
  .near-menu .item:first-child{
    border-right: solid 1px #ddd;
  }
  .near-menu .item .on-img{
    display: none;
  }
  .near-menu .item.on{
    color: #98c347;
  }
  .near-menu .item.on img{
    display: none;
  }
  .near-menu .item.on .on-img{
    display: inline;
  }
  .near-contain{
    position: absolute;
    left: 0;
    right: 0;
    top: 1.7rem;
    bottom: 1rem;
  }
  .shop-list{
    margin-top: 0.1rem;
  }
  .shop-list li{
    background: #fff;
    position: relative;
  }
  .shop-list li a{
    display: block;
    padding: 0.2rem 0.3rem;
    display: flex;
  }
  .shop-list li img{
    flex: 0 0 1.6rem;
    width: 1.6rem;
    height: 1.2rem;
  }
  .shop-list li .con{
    padding-left: 0.15rem;
  }
  .shop-list li .con h3{
    font-size: 15px;
  }
  .shop-list li .con p{
    font-size: 13px;
    color: #999;
    margin-top: 0.2rem;
  }
  .shop-list li:after{
    position: absolute;
    left: 0;
    bottom: -1px;
    width: 100%;
    height: 1px;
    border-top:solid 1px #ddd;
    transform: scaleY(0.5);
    content: '';
  }
  .pop{
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 50;
  }
  .pop .bg{
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.6);
  }
  .pop .bg.fade-enter-active,.pop .bg.fade-leave-active{
    transition: all 0.3s;
  }
  .pop .bg.fade-enter,.pop .bg.fade-leave-active{
    opacity: 0;
  }
  .pop-con{
    position: absolute;
    right: 0;
    top: 0;
    width: 5rem;
    height: 100%;
    background: #fff;
    padding: 1.6rem 0.3rem 0 0.3rem;
  }
  .pop-con.move-enter-active,.pop-con.move-leave-active{
    transition: all 0.3s;
  }
  .pop-con.move-enter,.pop-con.move-leave-active{
    transform: translateX(100%);
  }
  .c-item{
    margin-top: 0.25rem;
  }
  .c-list li{
    display: inline-block;
    width: 1.4rem;
    height: 0;
    border: solid 1px #ddd;
    border-radius: 0.3rem;
    text-align: center;
    line-height: 0;
    padding: 0.3rem 0;
    margin-right: 0.2rem;
    font-size: 12px;
    margin-top: 0.2rem;
  }
  .c-list li.on{
    color: #9fcc4a;
    border-color: #9fcc4a;
  }
  .c-list li:nth-child(3n){
    margin-right: 0;
  }
  .none{
    text-align: center;
    margin-top: 0.8rem;
    color: #999;
  }
</style>


