<template>
  <div>
    <v-header :title="'支付'"></v-header>
    <div class="pay-contain">
      <div class="pay-infor">
        <div class="sd">
          <div class="right">
            <span></span>
            <p>付款</p>
          </div>
          <div class="left">
            <span></span>
            <p>提交订单</p>
          </div>
          <div class="sd-line"><b></b></div>
        </div>
        <div class="i-money">
          订单金额:<em>￥{{money}}</em>
        </div>
      </div>
      <div class="pay-box">
        <img src="../../assets/checked.png" alt="" class="right">
        <img src="./wx.png" alt="" class="wx">
        <span>微信支付</span>
      </div>
    </div>
    <div class="btn-box">
      <div class="btn" @click="submit">提交</div>
    </div>
  </div>
</template>
<script>
  import header from '@/components/header/header'
  export default{
    components:{
      'v-header': header
    },
    data(){
      return {
        money: '',
        oid: ''
      }
    },
    created(){
      this.money = this.$route.query.money;
      this.oid = this.$route.query.id;
    },
    methods:{
      submit(){
        this.$router.push('/succ')
      }
    }
  }
</script>
<style scoped>
.pay-contain{
  padding-top: 0.9rem;
}
.pay-infor{
  padding: 0.4rem;
  height: 2.4rem;
  background: #eefcdc;
  text-align: center;
}
.pay-infor .sd{
  width: 4.4rem;
  margin: 0.4rem auto 0.2rem auto;
  position: relative;
}
.pay-infor .sd .left{
  width: 1.4rem;
  text-align: center;
}
.pay-infor .sd .left span{
  display: block;
  width: 0.2rem;
  height: 0.2rem;
  border-radius: 50%;
  background: #7ebe2b;
  margin: 0 auto;
}
.pay-infor .sd .right{
  width: 1.2rem;
  text-align: center;
  float: right;
}
.pay-infor .sd .right span{
  display: block;
  width: 0.2rem;
  height: 0.2rem;
  border-radius: 50%;
  background: #ddd;
  margin: 0 auto;
}
.pay-infor .sd p{
  line-height: 0.65rem;
  color: #999;
}
.pay-infor .sd .sd-line{
  width: 3rem;
  height: 0.05rem;
  background: #ddd;
  position: absolute;
  left: 0.75rem;
  top: 0.07rem;
}
.pay-infor .sd .sd-line b{
  display: block;
  width: 50%;
  height: 100%;
  background: #7ebe2b;
}
.pay-infor .i-money{
  text-align: center;
  font-size: 15px;
  line-height: 0.5rem;
}
.pay-infor .i-money em{
  color: #da0024;
  font-size: 16px;
  font-weight: 500;
}
.pay-box{
  padding: 0.2rem 0.3rem;
  background: #fff;
  height: 0.4rem;
}
.pay-box .right{
  float: right;
  width: 0.4rem;
  height: 0.4rem;
}
.pay-box .wx{
  width: 0.4rem;
  height: 0.4rem;
  display: inline-block;
  vertical-align: middle;
  margin-top: -2px;
}
.btn-box{
  padding: 0 0.3rem;
  margin-top: 0.8rem;
}
.btn-box .btn{
  padding: 0.45rem 0;
  line-height: 0;
  background: #9cc949;
  background: linear-gradient(left, #aedf50, #98c347); 
  height: 0;
  font-size: 16px;
  text-align: center;
  color: #fff;
  border-radius: 0.45rem;
}
</style>


